nav {
  width: 100%;
  height: 72px;
  position: fixed;
  top: 0;
  font-size: 17px;
  background-color: white;
  color: #333;
  padding-top: 12px;
  padding-left: 24px;
  border-top: solid 3px #3FBFBB;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  z-index: 200;
  transition-duration: 0.5s;
  transition-delay: 0;
  @endif {}  @endif {}  @endif {}  @endif {}  @endif {}
}

nav *, nav a {
  color: #555;
  text-decoration: none;
  font-weight: 400;
}

nav .row {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

nav .navbar-header {
  width: 264px;
  @endif {}  @endif {}
}

nav .navbar-header img {
  height: 50px;
  transition-duration: 0.5s;
  transition-delay: 0;
}

@media screen and (max-width: 1199px) and (min-width: 0px) {
  nav .navbar-header {
    width: 192px;
  }
}

nav .nav-leftpart {
  position: relative;
  vertical-align: middle;
}

nav .nav-leftpart > * {
  display: inline-block;
  vertical-align: top;
}

nav .navbar-search .highlight {
  padding: 2px 1px 4px 1px;
  border-bottom: solid 2px #3FBFBB;
}

nav .navbar-search-input {
  position: absolute;
  top: 12px;
  width: 100%;
  max-width: 648px;
  pointer-events: none;
  opacity: 0;
  transition-duration: 0.5s;
  transition-delay: 0;
  @endif {}
}

